function VegetableForm({ initialVeg, onSave, onCancel, token }) {
  const [nameSelection, setNameSelection] = useState(''); // Dropdown seimi
  const [customName, setCustomName] = useState(''); // zel isim girii
  const [name, setName] = useState('');
  const [count, setCount] = useState(1);
  const [notes, setNotes] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [imageFile, setImageFile] = useState(null);
  const [monthlyTasks, setMonthlyTasks] = useState(Array(12).fill(''));
  const [category, setCategory] = useState('genel');

  const handleApplySuggestions = () => {
    const template = getVegetableSuggestions(category);

    setMonthlyTasks((prev) =>
      prev.map((val, idx) => {
        if (val && val.trim()) return val;
        return template[idx] || '';
      })
    );
  };



  useEffect(() => {
    if (initialVeg) {
      const vegName = initialVeg.name || '';
      setName(vegName);

      // Listede varsa dropdown'dan se, yoksa "zel" se
      if (COMMON_VEGETABLE_NAMES.includes(vegName)) {
        setNameSelection(vegName);
        setCustomName('');
      } else {
        setNameSelection('zel');
        setCustomName(vegName);
      }

      setCount(typeof initialVeg.count === 'number' ? initialVeg.count : 1);
      setNotes(initialVeg.notes || '');
      setImageUrl(initialVeg.imageUrl || '');
      setImageFile(null);
      setCategory(initialVeg.category || 'genel');
      const tasks = Array(12).fill('');
      (initialVeg.maintenance || []).forEach((m) => {
        if (m.month >= 1 && m.month <= 12) {
          tasks[m.month - 1] = m.tasks;
        }
      });
      setMonthlyTasks(tasks);
    } else {
      setName('');
      setNameSelection('');
      setCustomName('');
      setCount(1);
      setNotes('');
      setImageUrl('');
      setImageFile(null);
      setMonthlyTasks(Array(12).fill(''));
      setCategory('genel');
    }
  }, [initialVeg]);

  // nameSelection veya customName deitiinde name'i gncelle
  // AYRICA: Kategori otomatik seilsin
  useEffect(() => {
    let newName = '';
    if (nameSelection === 'zel') {
      newName = customName;
    } else if (nameSelection) {
      newName = nameSelection;
    }
    setName(newName);

    // Otomatik Kategori Seimi (Sadece yeni eklemede veya kullanc deitirmediyse mantkl ama burada her isim deiiminde zorlayabiliriz)
    if (newName && VEGETABLE_CATEGORY_MAP[newName]) {
      setCategory(VEGETABLE_CATEGORY_MAP[newName]);
    }
  }, [nameSelection, customName]);

  const handleRemoveImage = async () => {
    // Yeni eklenen (daha kaydedilmemi) sebzede sadece local bilgiyi sil
    if (!initialVeg?._id) {
      setImageUrl('');
      setImageFile(null);
      return;
    }

    if (!window.confirm('Bu sebzenin resmini silmek istiyor musun?')) {
      return;
    }

    try {
      const res = await fetch(
        `${API_URL}/vegetables/${initialVeg._id}/image`,
        {
          method: 'DELETE',
          headers: {
            Authorization: `Bearer ${token}`
          }
        }
      );

      const data = await res.json().catch(() => ({}));

      if (!res.ok) {
        alert(data.message || 'Resim silinemedi.');
        return;
      }

      const updatedVeg = data.vegetable || {};
      setImageUrl(updatedVeg.imageUrl || '');
      setImageFile(null);
    } catch (err) {
      console.error('Sebze resmi silme hatas:', err);
      alert('Sebze resmi silinirken bir hata olutu.');
    }
  };




  const handleTaskChange = (index, value) => {
    const copy = [...monthlyTasks];
    copy[index] = value;
    setMonthlyTasks(copy);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      let finalImageUrl = imageUrl;

      if (imageFile) {
        console.log('Uploading vegetable image...');
        const formData = new FormData();
        formData.append('image', imageFile);

        const uploadRes = await fetch(`${API_URL}/upload`, {
          method: 'POST',
          headers: {
            Authorization: `Bearer ${token}`
          },
          body: formData
        });

        const uploadData = await uploadRes.json().catch((err) => {
          console.error('Image upload JSON parse error:', err);
          return {};
        });

        console.log('Upload response:', { ok: uploadRes.ok, data: uploadData });

        if (uploadRes.ok && uploadData.url) {
          finalImageUrl = uploadData.url;
        } else if (imageFile) {
          console.warn('Image upload failed, continuing without image');
        }
      }

      const maintenance = monthlyTasks
        .map((text, idx) =>
          text.trim() ? { month: idx + 1, tasks: text.trim() } : null
        )
        .filter(Boolean);

      await onSave({
        name,
        count: Number(count) || 0,
        notes,
        imageUrl: finalImageUrl,
        maintenance,
        category
      });
    } catch (err) {
      console.error('VegetableForm submit error:', err);
      alert('Bir hata olutu: ' + err.message);
    }
  };

  return (
    <form className="form-card" onSubmit={handleSubmit}>
      <h3>{initialVeg ? 'Sebzeyi Dzenle' : 'Yeni Sebze Ekle'}</h3>

      <label>
        Sebze Ad
        <select
          value={nameSelection}
          onChange={(e) => setNameSelection(e.target.value)}
          required
        >
          <option value="">-- Seiniz --</option>
          {COMMON_VEGETABLE_NAMES.map((vegName) => (
            <option key={vegName} value={vegName}>
              {vegName}
            </option>
          ))}
          <option value="zel"> zel (Elle gir)</option>
        </select>
      </label>

      {nameSelection === 'zel' && (
        <label>
          zel Sebze Ad
          <input
            type="text"
            value={customName}
            onChange={(e) => setCustomName(e.target.value)}
            placeholder="Sebze adn yazn"
            required
          />
        </label>
      )}

      <label>
        Adet
        <input
          type="number"
          min="0"
          value={count}
          onChange={(e) => setCount(e.target.value)}
        />
      </label>
      <label>
        Sebze Kategorisi
        <select
          value={category}
          onChange={(e) => setCategory(e.target.value)}
        >
          <option value="genel">Genel</option>
          <option value="yaprakli">Yaprakl (Marul, Lahana, Ispanak, Paz...)</option>
          <option value="kok">Kk Sebzeler (Havu, Pancar, Turp, Patates...)</option>
          <option value="meyveli">Meyveli Sebzeler (Domates, Biber, Patlcan, Bamya...)</option>
          <option value="kabakgil">Kabakgiller (Kabak, Salatalk, Kavun, Karpuz...)</option>
          <option value="baklagil">Baklagiller (Fasulye, Bezelye, Nohut, Mercimek...)</option>
          <option value="sogansi">Soans Bitkiler (Soan, Sarmsak, Prasa...)</option>
          <option value="aromatik">Aromatik Otlar (Maydanoz, Dereotu, Nane, Rezene...)</option>
          <option value="ozel">zel Sebzeler (Kukonmaz, Ravent...)</option>
        </select>
      </label>
      <label>
        Notlar
        <textarea
          rows="2"
          value={notes}
          onChange={(e) => setNotes(e.target.value)}
          placeholder="Genel notlar..."
        />
      </label>

      <label>
        Resim Se
        <input
          type="file"
          accept="image/*"
          onChange={(e) => setImageFile(e.target.files[0])}
        />
        <small className="help-text">
          Maksimum 1 MB, tercihen 1200x900 piksel ve JPEG / PNG / WEBP formatnda resim ykle.
        </small>
      </label>

      {imageUrl && (
        <div className="form-image-preview">
          <img
            src={`${BASE_URL}${imageUrl}`}
            alt="nizleme"
            loading="lazy"
            onError={(e) => {
              e.target.src = `${BASE_URL}/uploads/noimage.png`;
            }}
          />
          {/*  Sebze resmi sil butonu */}
          <button
            type="button"
            className="btn"
            style={{ marginTop: '4px' }}
            onClick={handleRemoveImage}
          >
            Resmi Sil
          </button>
        </div>
      )}





      <div className="form-actions" style={{ justifyContent: 'flex-start' }}>
        <button
          type="button"
          className="btn"
          onClick={handleApplySuggestions}
        >
           Kategoriye Gre Bakm nerilerini Doldur
        </button>
        <span className="muted">
          Bo olan aylar otomatik doldurulur, yazdklarn korunur.
        </span>
      </div>

      <div className="months-grid">
        {monthNames.map((mName, idx) => (
          <div key={mName} className="month-item">
            <div className="month-title">{mName}</div>
            <textarea
              rows="2"
              value={monthlyTasks[idx]}
              onChange={(e) => handleTaskChange(idx, e.target.value)}
              placeholder={`${mName} ay bakm...`}
            />
          </div>
        ))}
      </div>

      <div className="form-actions">
        <button type="button" className="btn" onClick={onCancel}>
          ptal
        </button>
        <button type="submit" className="btn primary">
          Kaydet
        </button>
      </div>
    </form>
  );
}

