/* -------------------- RAPORLAR -------------------- */



function Reports({ token }) {
  //  Aylk zet iin durumlar
  const [month, setMonth] = useState(new Date().getMonth() + 1);
  const [report, setReport] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  //  Gemi bakm raporu iin durumlar
  const [historyItems, setHistoryItems] = useState([]);
  const [historyLoading, setHistoryLoading] = useState(false);
  const [historyError, setHistoryError] = useState('');
  const [historyVisible, setHistoryVisible] = useState(false); //  Added for toggle

  //  Grafik grnm
  const [showCharts, setShowCharts] = useState(false);

  // ====== AYLIK RAPOR OLUTURMA ======
  const generateReport = async () => {
    setLoading(true);
    setError('');
    setReport(null);

    try {
      // Eer month = 0 ise (Tm Aylar), 12 ay iin rapor topla
      if (month === 0) {
        // const monthlyReports = [];

        // 12 ay iin paralel fetch
        const promises = [];
        for (let m = 1; m <= 12; m++) {
          promises.push(
            fetch(`${API_URL}/reports/monthly?month=${m}`, {
              headers: { Authorization: `Bearer ${token}` }
            })
              .then(res => res.json())
              .then(data => ({ month: m, ...data }))
              .catch(() => null)
          );
        }

        const results = await Promise.all(promises);

        // Baarl sonular topla
        const validReports = results.filter(r => r && r.tree && r.veg);

        if (validReports.length === 0) {
          throw new Error('Hibir ay iin rapor bulunamad.');
        }

        // Tm aylarn toplamn hesapla
        const totalTree = validReports.reduce((sum, r) => ({
          total: sum.total + r.tree.total,
          done: sum.done + r.tree.done,
          remaining: sum.remaining + r.tree.remaining
        }), { total: 0, done: 0, remaining: 0 });

        const totalVeg = validReports.reduce((sum, r) => ({
          total: sum.total + r.veg.total,
          done: sum.done + r.veg.done,
          remaining: sum.remaining + r.veg.remaining
        }), { total: 0, done: 0, remaining: 0 });

        const totalAll = {
          total: totalTree.total + totalVeg.total,
          done: totalTree.done + totalVeg.done,
          remaining: totalTree.remaining + totalVeg.remaining
        };

        // Yzdeleri hesapla
        totalTree.percent = totalTree.total > 0 ? Math.round((totalTree.done / totalTree.total) * 100) : 0;
        totalVeg.percent = totalVeg.total > 0 ? Math.round((totalVeg.done / totalVeg.total) * 100) : 0;
        totalAll.percent = totalAll.total > 0 ? Math.round((totalAll.done / totalAll.total) * 100) : 0;

        setReport({
          tree: totalTree,
          veg: totalVeg,
          total: totalAll,
          monthName: 'Tm Aylar (Yllk zet)'
        });
      } else {
        // Tek ay iin normal rapor
        const res = await fetch(`${API_URL}/reports/monthly?month=${month}`, {
          headers: { Authorization: `Bearer ${token}` }
        });

        const data = await res.json().catch(() => ({}));
        if (!res.ok) throw new Error(data.message || 'Rapor oluturulamad.');
        setReport(data);
      }
    } catch (err) {
      setError(err.message || 'Rapor alnrken hata olutu.');
    } finally {
      setLoading(false);
    }
  };

  // ====== GEM BAKIM RAPORU YKLE ======
  const loadHistory = async () => {
    setHistoryLoading(true);
    setHistoryError('');

    try {
      const res = await fetch(`${API_URL}/reports/history`, {
        headers: { Authorization: `Bearer ${token}` }
      });

      const data = await res.json().catch(() => ({}));
      if (!res.ok) throw new Error(data.message || 'Rapor alnamad.');
      setHistoryItems(data.items || data.months || []); // eski / yeni endpoint uyumu
    } catch (err) {
      setHistoryError(err.message || 'Bir hata olutu.');
    } finally {
      setHistoryLoading(false);
    }
  };

  // ====== CSV NDR ======
  const handleDownloadCSV = () => {
    if (!report) return;

    const rows = [
      ['Kategori', 'Toplam', 'Tamamlanan', 'Kalan', 'Yzde'],
      ['Aalar', report.tree.total, report.tree.done, report.tree.remaining, report.tree.percent],
      ['Sebzeler', report.veg.total, report.veg.done, report.veg.remaining, report.veg.percent],
      [
        'Toplam',
        report.total.total,
        report.total.done,
        report.total.remaining,
        report.total.percent
      ]
    ];

    const csvContent = rows.map((r) => r.join(';')).join('\n');
    const blob = new Blob([csvContent], {
      type: 'text/csv;charset=utf-8;'
    });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `aylik-ozet-raporu-${month}.csv`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  // ====== PDF / YAZDIR (print-to-PDF) ======
  const handleDownloadPDF = () => {
    if (!report) return;

    const win = window.open('', '_blank');
    if (!win) return;

    const html = `
      <html>
        <head>
          <title>Aylk zet Raporu - ${month}. Ay</title>
          <style>
            body { font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif; padding: 24px; }
            h1 { font-size: 20px; margin-bottom: 16px; }
            table { border-collapse: collapse; width: 100%; margin-top: 12px; }
            th, td { border: 1px solid #e5e7eb; padding: 8px 10px; text-align: center; font-size: 13px; }
            th { background: #f3f4f6; }
          </style>
        </head>
        <body>
          <h1>${month}. Ay Aylk zet Raporu</h1>
          <table>
            <thead>
              <tr>
                <th>Kategori</th>
                <th>Toplam</th>
                <th>Tamamlanan</th>
                <th>Kalan</th>
                <th>%</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Aalar</td>
                <td>${report.tree.total}</td>
                <td>${report.tree.done}</td>
                <td>${report.tree.remaining}</td>
                <td>%${report.tree.percent}</td>
              </tr>
              <tr>
                <td>Sebzeler</td>
                <td>${report.veg.total}</td>
                <td>${report.veg.done}</td>
                <td>${report.veg.remaining}</td>
                <td>%${report.veg.percent}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <th>Toplam</th>
                <th>${report.total.total}</th>
                <th>${report.total.done}</th>
                <th>${report.total.remaining}</th>
                <th>%${report.total.percent}</th>
              </tr>
            </tfoot>
          </table>
        </body>
      </html>
    `;

    win.document.open();
    win.document.write(html);
    win.document.close();
    win.focus();
    win.print(); // Kullanc "PDF olarak kaydet" seebilir
  };

  // ====== Grafik verileri ======
  const chartData = report
    ? {
      labels: ['Aalar', 'Sebzeler', 'Toplam'],
      datasets: [
        {
          label: 'Tamamlanma (%)',
          data: [report.tree.percent, report.veg.percent, report.total.percent],
          backgroundColor: ['#22c55e', '#3b82f6', '#6366f1'],
          borderRadius: 10,
          maxBarThickness: 45
        }
      ]
    }
    : null;

  const doughnutData = report
    ? {
      labels: ['Tamamlanan', 'Kalan'],
      datasets: [
        {
          data: [report.total.done, report.total.remaining],
          backgroundColor: ['#22c55e', '#e5e7eb'],
          borderWidth: 0
        }
      ]
    }
    : null;

  const barOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: { legend: { display: false } },
    scales: {
      x: { grid: { display: false } },
      y: { beginAtZero: true, max: 100, ticks: { stepSize: 20 } }
    }
  };

  const doughnutOptions = {
    plugins: {
      legend: { position: 'bottom', labels: { boxWidth: 14, font: { size: 11 } } }
    },
    cutout: '70%',
    radius: '90%'
  };

  return (
    <div className="page reports-page">
      <div className="modern-tab-header reports">
        <h2> Raporlar</h2>
        <p className="subtitle">statistikler, analizler ve raporlama</p>
      </div>

      {/* ===================== AYLIK RAPOR ===================== */}
      <div className="card report-card">
        <div className="section-header report-header">
          <div>
            <h3>
              <span className="modern-section-icon" style={{ fontSize: '1.2rem', marginRight: '8px' }}></span>
              Aylk zet Raporu
            </h3>
            <p className="muted" style={{ marginTop: 4 }}>
              Setiin ay iin aa ve sebze bakmlarnn ksa zeti.
            </p>
          </div>

          <div className="report-header-right">
            <div className="modern-select-container">
              <span style={{ fontSize: '1.2rem' }}></span>
              <select
                value={month}
                onChange={(e) => setMonth(Number(e.target.value))}
                className="report-month-select"
              >
                <option value={0}>Tm Aylar (Yllk zet)</option>
                {monthNames.map((m, idx) => (
                  <option key={idx + 1} value={idx + 1}>
                    {idx + 1}. {m}
                  </option>
                ))}
              </select>
            </div>

            <button
              className="btn"
              type="button"
              onClick={report ? () => setReport(null) : generateReport}
              disabled={loading}
            >
              {loading ? 'Hazrlanyor...' : (report ? 'Raporu Gizle' : 'Raporu Olutur')}
            </button>
          </div>
        </div>

        {error && <p className="error-text">{error}</p>}

        {/* Rapor tablosu */}
        {report && !loading && (
          <>
            <div className="report-table-wrapper">
              <table className="report-table">
                <thead>
                  <tr>
                    <th>Kategori</th>
                    <th>Toplam</th>
                    <th>Tamamlanan</th>
                    <th>Kalan</th>
                    <th>%</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Aalar</td>
                    <td>{report.tree.total}</td>
                    <td>{report.tree.done}</td>
                    <td>{report.tree.remaining}</td>
                    <td>%{report.tree.percent}</td>
                  </tr>
                  <tr>
                    <td>Sebzeler</td>
                    <td>{report.veg.total}</td>
                    <td>{report.veg.done}</td>
                    <td>{report.veg.remaining}</td>
                    <td>%{report.veg.percent}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <th>Toplam</th>
                    <th>{report.total.total}</th>
                    <th>{report.total.done}</th>
                    <th>{report.total.remaining}</th>
                    <th>%{report.total.percent}</th>
                  </tr>
                </tfoot>
              </table>
            </div>

            {/* Export & grafik butonlar */}
            <div className="report-actions-row">
              <button className="btn" type="button" onClick={handleDownloadCSV}>
                CSV olarak indir
              </button>
              <button className="btn" type="button" onClick={handleDownloadPDF}>
                PDF olarak indir
              </button>
              <button
                className="btn secondary-btn"
                type="button"
                onClick={() => setShowCharts((v) => !v)}
              >
                {showCharts ? 'Grafikleri gizle' : 'Grafikleri gster'}
              </button>
            </div>

            {/* Grafikler */}
            {showCharts && (
              <div className="report-charts">
                <div className="report-chart-card">
                  <h4>Genel Tamamlanma</h4>
                  <p className="muted" style={{ marginBottom: 4 }}>
                    {report.total.done}/{report.total.total} grev tamamland.
                  </p>
                  <div className="chart-wrapper chart-wrapper-sm">
                    <Doughnut data={doughnutData} options={doughnutOptions} />
                    <div className="chart-center-label">
                      %{report.total.percent}
                    </div>
                  </div>
                </div>

                <div className="report-chart-card">
                  <h4>Kategori Bazl Tamamlanma</h4>
                  <p className="muted" style={{ marginBottom: 4 }}>
                    Aa  Sebze  Toplam karlatrmas
                  </p>
                  <div className="chart-wrapper chart-wrapper-sm">
                    <Bar data={chartData} options={barOptions} />
                  </div>
                </div>
              </div>
            )}
          </>
        )}

        {!report && !loading && !error && (
          <p className="muted">
            Bir ay seip <strong>Raporu Olutur</strong> butonuna basarak
            zet raporu grebilirsin.
          </p>
        )}
      </div>

      {/* ===================== GEM RAPOR ===================== */}
      <div className="card report-card">
        <div className="section-header">
          <h3>
            <span className="modern-section-icon" style={{ fontSize: '1.2rem', marginRight: '8px' }}></span>
            GEM BAKIM RAPORU (Aa + Sebze)
          </h3>
          <button
            className="btn"
            type="button"
            onClick={() => {
              const next = !historyVisible;
              setHistoryVisible(next);
              if (next && historyItems.length === 0) loadHistory();
            }}
            disabled={historyLoading}
          >
            {historyLoading ? 'Ykleniyor...' : (historyVisible ? 'Raporu Gizle' : 'Raporu Getir')}
          </button>
        </div>

        {historyVisible && (
          <>
            {historyError && <p className="error-text">{historyError}</p>}

            {historyItems.length === 0 && !historyLoading && !historyError && (
              <p className="muted">Tamamlanm bakm kayd bulunamad.</p>
            )}

            {historyItems.length > 0 && historyItems[0].type && (
              <ul className="history-list">
                {historyItems.map((h, i) => (
                  <li key={i} className="history-item">
                    <span className="history-type">{h.type}</span>
                    <span className="history-name">
                      {h.kind}  {h.name}
                    </span>
                    <span className="history-task">{h.task}</span>
                    <span className="history-month">{h.month}. Ay</span>
                    <span className="history-date">
                      {h.completedAt
                        ? new Date(h.completedAt).toLocaleDateString('tr-TR')
                        : '-'}
                    </span>
                  </li>
                ))}
              </ul>
            )}
          </>
        )}
      </div>
    </div>
  );
}







